@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    background-color: v.$secundario;
    color: v.$blanco;


    .footer-grid {
        padding: 5rem 0;
        @include m.tablet {
            @include m.grid(4, 4rem);
        }
    }

    .logo {
        max-width: 20rem;
        margin: 0 auto 5rem auto;
        @include m.tablet {
            margin-bottom: 0;
        }
    }
    .footer-widget {
        margin-bottom: 2rem;
        @include m.tablet {
            margin-bottom: 0;
            
        }

        h3 {
            color: v.$primario;            
            text-align: center;
            @include m.tablet {
                margin-bottom: 3rem;
                text-align: left;
                
            }
        }
    }

    nav {
        a {
            display: block;
            color: v.$blanco;
            margin-bottom: 1rem;
            text-align: center;
            @include m.tablet {                
                display: block; 
                text-align: left;               
            }

            &:last-of-type {
                margin-bottom: 0;
            }
        }
    }

    .copyright {
        text-align: center;
        color: v.$blanco;
        margin: 0 auto;
        border-top: .2rem solid v.$primario;
        padding: 1rem ;
        max-width: 120rem;
        font-size: 1.3rem;
        text-transform: uppercase;
    }
}